import React from 'react';
import styled, {css} from 'styled-components';
import doctors from './doctors.json';
import ProductItem from './ProductItem';

const ProductBlock = styled.div`
    border-top: 103.2px solid green;
    .tabs { display:flex; justify-content: center; }
    .content { display: flex; flex-wrap: wrap; }
`;

const List = styled.div`
    padding: 10px 20px; background: #ddd; border-radius: 10px;
    cursor: pointer; margin: 40px 20px;
    ${
        props => props.active && css`background: #333; color: #fff;`
    }
`;

const tabTit = [
    {id:0, title:"메뉴1", part:doctors.part1},
    {id:1, title:"메뉴2", part:doctors.part2},
    {id:2, title:"메뉴3", part:doctors.part3}
]

const Productsection = ({activeTab, onSelect}) => {

    return (
        <ProductBlock>
            <div className="row">
                <div className="tabs">
                    {
                        tabTit.map(
                            (data) => <List
                                key={data.id}
                                active={activeTab===data.id}
                                onClick={ () => onSelect(data.id)}>
                              {data.title}
                            </List>
                        )
                    }
                </div>
                <div className="content">
                    {
                        tabTit[activeTab].part.map(
                            (data, index) => <ProductItem
                                key={index}
                                name={data.name}
                                depart={data.depart}
                                about={data.about}
                                photo={data.photo}
                            />
                        )
                    }
                </div>
            </div>
        </ProductBlock>
    );
};

export default Productsection;